@keyframes heartbeat {
  0% {
    transform: rotate(25deg) scale( .75);
  }
  20% {
    transform: rotate(35deg) scale( 1);
  }
  40% {
    transform: rotate(25deg) scale( .75);
  }
  60% {
    transform: rotate(30deg) scale( 1);
  }
  80% {
    transform: rotate(25deg) scale( .75);
  }
  100% {
    transform: rotate(25deg) scale( .75);
  }
}

#heart {
  font-size: 8vmin;
  animation: heartbeat 1s infinite;
}
